<template>
  <div class="order-list" style="padding-top: 44px">
    <back-header title="全部订单">
      <img v-if="false" src="./search@3x.png" class="search" alt="">
    </back-header>
    <ul class="tabs" style="height: 44px">
      <li @click="changeTab(0)" :class="{active: type === 0}">全部 <i></i></li>
      <li @click="changeTab(1)" :class="{active: type === 1}">待付款 <i></i></li>
      <li @click="changeTab(2)" :class="{active: type === 2}">待发货 <i></i></li>
      <li @click="changeTab(3)" :class="{active: type === 3}">待收货 <i></i></li>
      <li @click="changeTab(4)" :class="{active: type === 4}">退款 <i></i></li>
    </ul>
    <scroll ref="scroll" clickable :pull-down-refresh="{threshold: 70, stop: 56}" @pullingDown="pullingDown" :is-finished="finished" @scrollToEnd="scrollToEnd" class="order-scroll">
      <div @click="$router.push('/order/' + item.id + '?type=' + (type))" v-for="item in list" :key="item.id" class="order-item">
        <order-store :status-text="status[item.status]" :store="item.shop" />
        <div v-for="goods in item.goods" :key="goods.id" class="goods">
          <order-item :size-drop="false" price-show :goods="goods"/>
          <div class="status" v-if="item.status === 4">
<!--            <button @click.stop="$router.push('/logistics')">查看物流</button>-->
            <button class="red" v-if="item.status === 4 && goods.evaluate_status === 0" @click.stop="evaluation(goods, item.id)">评价</button>
          </div>
        </div>
        <div class="status">
          <template v-if="item.status === 0">
            <p>请在{{ item.cancelTime }}前支付</p>
            <button>取消订单</button>
            <button class="red">付款</button>
          </template>
<!--          <template v-if="item.status === 1">-->
<!--            <button @click.stop="$router.push({ name: 'order-refunds' })">申请退款</button>-->
<!--          </template>-->
          <template v-if="item.status === 3">
            <button @click.stop="openExpress(item)">查看物流</button>
            <button class="red">确认收货</button>
          </template>
          <template v-if="item.status === 5 || item.status === 1">
            <button class="red">查看详情</button>
          </template>
        </div>
      </div>
      <div class="none" v-if="!list.length">
        <img src="./dingdan@2x.png" alt="">
        <p>暂无相关订单</p>
      </div>
    </scroll>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import OrderStore from '../../components/order-store/order-store'
import OrderItem from '../../components/order-item/order-item'
import { post } from '../../common/js/http'
import moment from 'moment'

const PAGE_SIZE = 10

export default {
  name: 'order-list',
  components: {
    BackHeader,
    Scroll,
    OrderStore,
    OrderItem
  },
  data() {
    return {
      list: [],
      status: ['待付款', '待发货', '订单已关闭', '卖家已发货', '交易成功', '退款'],
      type: 0,
      page: 1,
      finished: true
    }
  },
  activated () {
    if (Number(this.$route.params.type) !== this.type && !this.$router.isBack) {
      this.type = Number(this.$route.params.type)
    }
    if (!this.$router.isBack || this.$router.needRefresh) {
      this.page = 1
      this.getList()
    }
    this.$router.needRefresh = false
  },
  created() {
    this.type = Number(this.$route.params.type)
  },
  mounted () {
    this.getList()
  },
  methods: {
    openExpress(item) {
      if (!item.express_no) return
      this.$router.push({
        name: 'logistics',
        params: {
          cover: item.goods[0].cover,
          order: item
        }
      })
    },
    changeTab(tab) {
      if (tab === this.type) return
      this.page = 1
      this.type = tab
      this.list = []
      this.$refs.scroll.scrollTo([0, 0])
      this.getList()
    },
    pullingDown() {
      this.page = 1
      this.pulling = true
      this.getList()
    },
    scrollToEnd() {
      if (this.loading) return
      this.loading = true
      this.page++
      this.getList()
    },
    getList() {
      let loading = null
      if (this.page === 1 && !this.pulling) {
        loading = this.$skeLoading('.order-list', '88px')
      }
      post('/shop/user/getOrderList', {
        type: this.type,
        page: this.page,
        size: PAGE_SIZE
      })
        .then(({ data: { data } }) => {
          const list = data.map(item => {
            if (item.suborder) {
              return {
                ...item,
                status: 5,
                id: item.goods.id,
                goods: [{
                  ...item.goods,
                  title: item.goods.goods.title,
                  goods: null,
                  size: item.goods.color + (item.goods.size || ''),
                  cover: item.goods.goods.thumb_urls.split(',')[0]
                }]
              }
            }
            if (item.status === 3) {
              item.status = 4
            }
            if (item.status === 1 && item.delivery_status) {
              item.status = 3
            }
            item.cancelTime = moment(new Date(item.create_time.replace(/-/g, '/')).getTime() + 15 * 60 * 1000).format('YYYY-MM-DD hh:mm:ss')
            item.goods = item.goods.map(goods => {
              return {
                ...goods,
                title: goods.goods.title,
                goods: null,
                size: goods.color + (goods.size || ''),
                cover: goods.goods.thumb_urls.split(',')[0]
              }
            })
            return item
          })
          this.list = this.page === 1 ? list : this.list.concat(list)
          this.finished = data.length < PAGE_SIZE
          this.loading = false
          loading && loading.close()
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
          this.pulling = false
          this.$refs.scroll.closeRefresh()
        })
        .catch(() => {
          loading && loading.close()
        })
    },
    evaluation(goods, id) {
      this.$router.push({
        name: 'evaluation',
        params: {
          goods,
          id
        }
      })
    }
  }
}
</script>

